<template>
  <section class="catalog-info" :class="{open: menuState}">
    <div class="catalog-info__field">
      <h2 class="catalog-info__title">My basket</h2>
      <Cart/>
    </div>
    <div class="catalog-info__field">
      <h2 class="catalog-info__title">Promo code</h2>
      <promo-code/>
    </div>
    <div class="catalog-info__field">
      <div class="catalog-info__total">
        <div class="catalog-info__total-item">
          <span>Subtotal</span>
          <span>$ {{ cartTotalPrice | numberFormat }}</span>
        </div>
        <div class="catalog-info__total-item">
          <span>Tax</span>
          <span>$ {{ tax | numberFormat }}</span>
        </div>
        <div class="catalog-info__total-item">
          <span>Shipping</span>
          <span>$ {{ shipping | numberFormat }}</span>
        </div>
        <div v-if="discount > 0" class="catalog-info__total-item">
          <span>Discount</span>
          <span>-{{ discount }}%</span>
        </div>
        <div class="catalog-info__total-sum">
          <span>Total</span>
          <span>$ {{ totalPrice | numberFormat }}</span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Cart from '@/components/cart/Cart'
import PromoCode from '@/components/common/PromoCode'
import { mapGetters } from 'vuex'
import numberFormat from '@/helpers/numberFormat'

export default {
  name: 'CatalogInfo',
  filters: { numberFormat },
  components: {
    Cart,
    PromoCode
  },
  data () {
    return {
      tax: 100,
      shipping: 150
    }
  },
  computed: {
    ...mapGetters(['cartTotalPrice']),
    discount () {
      return this.$store.state.discount.percent
    },
    totalPrice () {
      const price = this.cartTotalPrice + this.tax + this.shipping
      return this.discount > 0 ? (price * (1 - this.discount / 100)) : price
    },
    menuState () {
      return this.$store.state.menuState
    }
  }
}
</script>

<style lang="scss">
.catalog-info {
  width: 410px;
  border-left: 1px solid $c-gray;
  @include respond-to(lg) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateY(100%);
    background: $c-white;
    transition: transform 0.6s cubic-bezier(0.65, 0, 0.35, 1);
    overflow-y: scroll;
    overscroll-behavior: contain;
    padding-bottom: 40px;
    &.open {
      transform: translateY(0%);
    }
  }

  &__total {
    & > * {
      display: flex;
      justify-content: space-between;
      @include font-size-get('h3');
    }

    & > * + * {
      margin-top: 18px;
    }

    &-sum {
      font-weight: bold;
    }
  }

  & > * {
    padding: 40px;

    &:not(:last-child) {
      border-bottom: 1px solid $c-gray;
    }
  }

  &__title {
    font-weight: bold;

    & + * {
      margin-top: 30px;
    }
  }
}
</style>
