<template>
  <div class="cart">
    <h3 class="cart__subtitle" v-show="cart.length < 1">Oops, cart is empty :(</h3>
    <transition-group name="card" tag="ul" class="cart__list">
      <CartItem :item="item" v-for="item in cart" :key="item.name"/>
    </transition-group>
  </div>
</template>

<script>
import CartItem from '@/components/cart/CartItem'

export default {
  name: 'Cart',
  components: { CartItem },
  computed: {
    cart () {
      return this.$store.state.cart
    }
  }
}
</script>

<style lang="scss">
.cart {
  position: relative;

  &__title {
    font-weight: bold;
  }

  &__subtitle {
    font-weight: 400;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__item {
    transition: all 0.5s cubic-bezier(0.65, 0, 0.35, 1);

  }

  &__list {
    overflow-x: hidden;
  }
}

.card-enter, .card-leave-to
{
  opacity: 0;
  transform: translateX(-100%);
}

.card-enter-to {
  opacity: 1;
  transform: translateX(-0%);

}

.card-move {
  opacity: 1;
  transition: all 0.6s cubic-bezier(0.65, 0, 0.35, 1);

}
</style>
