<template>
  <div class="form__input-shell" :class="{error: status.success === 0, success: status.success === 1} ">
    <input class="form__input" v-model="dataValue" :type="type" :name="name"
           :placeholder="placeholder">
    <span v-if="status.msg.length > 0">{{ status.msg }}</span>
  </div>
</template>
<script>
export default {
  props: ['status', 'placeholder', 'value', 'type', 'name'],
  computed: {
    dataValue: {
      get () {
        return this.value
      },
      set (v) {
        this.$emit('input', v)
      }
    }
  }
}
</script>
<style lang="scss">
.form__input {
  border-radius: 3px;
  background: $c-gray-medium;
  padding: 8px 10px;
  border: none;
  outline: none;

  &-shell {
    display: flex;
    flex-direction: column;

    span {
      font-size: 10px;
      margin-top: 6px;
    }

    &.success {
      input {
        border: 1px solid $c-success;
      }

      span {
        color: $c-success;
      }
    }

    &.error {
      input {
        border: 1px solid $c-error;
      }

      span {
        color: $c-error;
      }
    }
  }

  &::placeholder {
    @include font-size-get('form');
    color: $c-dark-blue;
  }
}
</style>
