<template>
  <form @submit.prevent="changePromoCode(promoCode)" class="promo-code form">
    <base-form-text placeholder="Placeholder Text" :status="status"
                    v-model="promoCode" type="text" name="promo-code"/>
    <base-form-submit />
  </form>
</template>

<script>
import BaseFormText from '@/components/form/BaseFormText'
import BaseFormSubmit from '@/components/form/BaseFormSubmit'
import { mapActions } from 'vuex'

export default {
  name: 'PromoCode',
  components: {
    BaseFormText,
    BaseFormSubmit
  },
  data () {
    return {
      promoCode: '',
      error: '',
      checkPromoState: false
    }
  },
  computed: {
    status () {
      return this.$store.state.discount.status
    }
  },
  methods: {
    ...mapActions(['changePromoCode'])
  }
}
</script>

<style lang="scss">
.promo-code {
  display: flex;
}

.form {
  &__input {
    width: 225px;
    height: 40px;
  }

  &__btn {
    margin-left: 10px;
    height: 40px;
  }
}
</style>
