<template>
  <li class="cart__item">
    <div class="cart__item-img">
      <img :src="item.img" :alt="item.name">
    </div>
    <div class="cart__item-info">
      <h3 class="cart__item-name">
        {{ item.name }}
        <button class="cart__item-del" type="button" aria-label="Удалить товар из корзины"
                @click.prevent="deleteProductOfCart(item.id)">
        </button>
      </h3>
      <div class="cart__item-count">
        <ProductCount :productAmount.sync="productAmount"/>
        <span class="cart__item-price">
        $ {{ (item.amount * item.price) | numberFormat }}
      </span>
      </div>

    </div>
  </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat'
import { mapActions } from 'vuex'
import ProductCount from '@/components/product/ProductCount'

export default {
  filters: { numberFormat },
  components: { ProductCount },
  props: ['item'],
  computed: {
    productAmount: {
      get () {
        return this.item.amount
      },
      set (value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.id,
          amount: value
        })
      }
    }
  },
  methods: {
    ...mapActions(['deleteProductOfCart'])
  }
}
</script>
<style lang="scss">
.cart__item {
  display: flex;

  &-info {
    flex: 1;
    margin-left: 12px;
  }

  &-img {
    width: 100px;
    height: 100px;
    padding: 0px 5px;

    img {
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: contain;
    }
  }

  &-name {
    display: flex;
    font-weight: 400;
    justify-content: space-between;
  }

  &-del {
    width: 18px;
    height: 18px;
    cursor: pointer;
    transition: filter .2s ease-in-out;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url("data:image/svg+xml,%3Csvg width='19' height='18' viewBox='0 0 19 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.756 9L18.7001 16.9629L17.6639 18L9.70834 10.0487L1.75274 18L0.716553 16.9629L8.66064 9L0.716553 1.03713L1.75274 0L9.70834 7.95134L17.6639 0L18.7001 1.03713L10.756 9Z' fill='%23CFCFCF'/%3E%3C/svg%3E ");

    &:hover {
      filter: brightness(0.5);
    }
  }

  &-count {
    display: flex;
    align-items: center;
    margin-top: 15px;
  }

  &-price {
    margin-left: 25px;
    @include font-size-get('h3');
  }
}
</style>
